.leaflet-container {
  height: calc(100vh - 80px) !important;
  width: 100%;
  z-index: 0;
}

@media (max-width: 768px) {
  .leaflet-container {
    height: calc(100vh - 60px) !important;
    width: 100%;
    z-index: 0;
  }
}